export interface JSONData<T> {
    [key: string]: T | any
}

export type TagType = 'primary' | 'gray' | 'success' | 'warning' | 'danger'

export default class FieldBase {
    /**
     * apiFunction: () => Promise
     * @protected
     */
    protected serve: string | Function;
    /**
     * 
     * @protected
     */
    protected id: string | undefined;
    /**
     * 
     * @protected
     */
    protected label: string | undefined;
    /**
     * get
     * @protected
     */
    protected method: string | undefined;
    /**
     * 
     * @protected
     */
    protected requestParams: any;
    /**
     * key
     * @protected
     */
    protected responseKey: string;
    /**
     * datadata
     * @protected
     */
    protected dataField: string | undefined;
    /**
     * 
     * @protected
     */
    protected class: string;
    /**
     * 
     * @protected
     */
    protected isCustom: boolean;
    /**
     * 
     * @protected
     */
    protected customData: JSONData<any>;
    /**
     * (data, list)datalist
     * @protected
     */
    protected render: undefined | ((data: any, list: any[]) => string);
    /**
     * tagel-tag
     * @protected
     */
    protected tagTypes: JSONData<TagType> | undefined;

    constructor() {
        this.serve = "";
        this.id = "";
        this.label = "";
        this.method = 'get';
        this.requestParams = undefined;
        this.responseKey = 'data';
        this.dataField = 'data';
        this.class = "";
        this.isCustom = false;
        this.customData = {};
        this.render = undefined;
        this.tagTypes = undefined;
    }

    /**
     * tagel-tag
     * @param tags
     */
    tags(tags: JSONData<TagType>) {
        this.tagTypes = tags;
        return this;
    }

    /**
     * 
     * @param render
     */
    renders(render: ((data: any, list: any[]) => string)): FieldBase {
        this.render = render;
        return this;
    }
}
